import * as React from 'react';
import Staff from '@miq/staffjs';

import { Icons } from '@miq/componentjs';
import { SharedDataCtx } from '@miq/contextjs';
import { useSearchParams } from 'react-router-dom';
import { useHitListRequest, hitsByPath } from '@miq/staffjs';
import { Response } from '@miq/utiljs';

const TopViewsSection = () => {
  const { res } = useHitListRequest('/shop/');

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const paths = hitsByPath(r.items);

  return (
    <Staff.Section title="Top Views" text="This data is based on the last 100 hits." className="my-2">
      {Object.entries(paths)
        .sort((a, b) => (a?.[1].length > b?.[1].length ? -1 : 1))
        .map((i) => {
          const [path, hits] = i;
          const { session_data } = hits.find((i) => i.session_data) || {};
          const { name, img } = session_data;

          return (
            <Staff.Section
              title={
                <div className="text-sm">
                  {name && <div className="">{session_data?.name}</div>}
                  <div className="fw-normal text-muted">{path}</div>
                </div>
              }
              actions={
                <div>
                  {img && (
                    <div style={{ maxWidth: 20 }}>
                      <Staff.Img.Image {...{ src: img }} className="rounded" />
                    </div>
                  )}
                </div>
              }
              border
              className="mb-1"
              key={path}
            />
          );
        })}
    </Staff.Section>
  );
};

export default function ShopStaffIndexView() {
  const ctx = React.useContext(SharedDataCtx);
  const { isLoaded, products, orders, cats, shopy_settings } = ctx as any;
  const [params] = useSearchParams();

  const tab = params.get('tab');

  if (!isLoaded) return null;

  // console.log(ctx);

  return (
    <Staff.View
      title="Shop"
      back="/staff/"
      actions={
        <Staff.NavLink to="settings/">
          <Icons.Gear />
        </Staff.NavLink>
      }
    >
      <Staff.Section>
        <div className="d-grid grid-2 grid-sm-3 grid-md-4 grid-lg-6">
          {shopy_settings?.config?.has_suppliers && (
            <CardLink
              title="Supplier Orders"
              tab="./"
              link={{ to: 'orders/', text: 'View all orders', perms: ['store.view_supplierorder'] }}
            >
              <h3>{orders?.count || 0}</h3>
            </CardLink>
          )}

          <CardLink
            title="Products"
            tab="./?tab=products"
            link={{ to: 'products', text: 'View all products', perms: ['store.view_product'] }}
          >
            <h3>{products?.count || 0}</h3>
          </CardLink>

          <CardLink
            tab="./?tab=categories"
            title="Categories"
            link={{ to: 'categories', text: 'View all categories', perms: ['store.view_category'] }}
          >
            <h3>{cats?.count || 0}</h3>
          </CardLink>
        </div>
      </Staff.Section>

      <TopViewsSection />

      <Staff.Section title="Top Categories" className="my-2">
        {cats?.cat_count
          ?.filter((i) => i.category__name)
          ?.map(({ category__name, count }, i) => (
            <div key={i}>
              {category__name}: {count}
            </div>
          ))}
      </Staff.Section>
    </Staff.View>
  );
}

const CardLink: React.FC<{
  title: string;
  tab?: string;
  link: { to: string; text: string; perms: string[] };
}> = ({ children, title, link, tab = './' }) => {
  const { to } = link;
  return (
    <div className="card-link mb-3">
      <Staff.NavLink to={tab}>
        {title && <div className="card-link-title">{title}</div>}
        {children}
      </Staff.NavLink>

      <Staff.Link to={to} requiredPerms={link.perms}>
        {link?.text && `${link.text} Â»`}
      </Staff.Link>
    </div>
  );
};
